import Image from "next/image";

const Header = () => {
    return (
        <header className="opacity-25 bg-gradient-to-r from-cyan-500 to-blue-500 bg-opacity-50 backdrop-blur-md w-full h-20">
            <Image
                src="/path/to/your/image.jpg" // Replace with your image path
                alt="Header image"
                width={100}
                height={100}
                className="rounded-full mx-auto"
            />
            <h1 className="opacity-100 text-white text-2xl font-bold text-center">Welcome, User47563256</h1>
        </header>
    );
};

export default Header;
